<template>
	<view class="content">
		<view class="videos">
			<view class="person-item" v-for="(v,i) in deviceList" :key='"v"+i'>
				<video  id="myVideo" style="z-index: 99 !important;" :src="v.rtmpHd" autoplay="true"></video>
				<view></view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				companyId:'',
				deviceList:[],
				videoContext:'',//视频实例
				touchStartTime:0,//初始点击时间
			}
		},
		onLoad(e) {
			uni.setNavigationBarTitle({
			    title: e.companyName
			});
			this.companyId = e.companyId;
			this.getDevices()
		},
		onReady() {
			this.videoContext = uni.createVideoContext('myVideo')
		},
		methods: {
			// 获取企业监控列表
			getDevices(){
				let api = '/monitor/company/device';
				let data = {companyId:this.companyId};
				this.http(api,"GET",data).then(res=>{
					console.log(res);
					if(res.code==200){
						this.deviceList = res.data;
					}
				})
			},
		}
	}
</script>

<style scoped>
	.videos{
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		width: 100%;
		padding-left: 16rpx;
		box-sizing: border-box;
	}
	.videos .person-item{
		width: 348rpx;
		height: 240rpx;
		margin-right: 16rpx;
		margin-left: 0;
		position: relative;
	}
	.videos .person-item video{
		width: 322rpx;
		height: 216rpx;
		margin: 12rpx 0 0 12rpx;
		/* background: url(../../../static/home/500.jpg) no-repeat; */
		background-size: 100% 100%;
		
	}
</style>
